  HELP_PARAGRAPH_2: 'การพนันออนไลน์ควรได้รับการปฏิบัติเพื่อความบันเทิง และไม่ใช่แหล่งที่มาหลักของรายได้ เราสนับสนุนให้ผู้เล่นของเราติดตามการพนันของพวกเขาอย่างต่อเนื่องโดยถามคำถามต่อไปนี้กับตัวเอง:\n',
  HELP_PARAGRAPH_3: 'นี่เป็นเพียงคำถามสำคัญสองสามข้อที่นักการพนันควรถามตนเองทุกครั้ง และหากเป็นเช่นนั้นคำตอบส่วนใหญ่คือใช่บางทีอาจถึงเวลาที่คุณต้องโทรกลับไปที่การเยี่ยมชมคาสิโน!\n',
  HELP_PARAGRAPH_4: 'สำหรับผู้ที่ประสบกับปัญหาการพนันอย่างมาก HL8 แนะนำหน่วยงานต่อไปนี้เพื่อขอความช่วยเหลือ:',
  HELP_PARAGRAPH_6: 'HL8 จะระงับบัญชีใด ๆ ที่ได้รับการพิสูจน์แล้วว่าดำเนินการโดยทุกคนที่มีอายุต่ำกว่า 18 ปี บริษัท ขอสงวนสิทธิ์ในการตรวจสอบข้อมูลเจ้าของบัญชีแบบสุ่มผ่านทีมสนับสนุน KYC การให้เอกสารเท็จจะทำให้บัญชีถูกระงับทันที\n',
  HELP_PARAGRAPH_7: 'ไม่ใช่เรื่องแปลกสำหรับครอบครัวที่จะแบ่งปันคอมพิวเตอร์ในบ้าน จากข้อเท็จจริงนี้เราสนับสนุนให้ผู้ใหญ่ในบ้านคัดกรอง / จำกัด การใช้คอมพิวเตอร์ของผู้เยาว์ที่อ่อนไหวด้วยการใช้แอปพลิเคชันเหล่านี้\n',
  HELP_PARAGRAPH_LINK_1: 'www.gamecare.org.uk',
  HELP_PARAGRAPH_LINK_2: 'www.gamblingtherapy.org',
  HELP_PARAGRAPH_LI_1: 'ฉันกำลังเล่นการพนันเพื่อหลีกเลี่ยงการคิดถึงเกี่ยวกับปัญหาของฉัน?',
  HELP_PARAGRAPH_LI_2: 'ฉันเคยโดดเรียน หรือทำงานเพื่อเล่นการพนัน หรือไม่?',
  HELP_PARAGRAPH_LI_3: 'การพนันของฉันเป็นที่รู้จักอย่างกว้างขวางในหมู่เพื่อนของฉัน?',
  HELP_PARAGRAPH_LI_4: 'ฉันเคยพนันเงินโดยที่ฉันรู้ว่าเงินนั้นต้องเอาใช้กับสิ่งที่สำคัญกว่า?',
  HELP_PARAGRAPH_LI_5: 'ฉันเคยเล่นการพนันจนกระทั่งเงินในกระเป๋าของฉันหมดไป หรือไม่?',
  HELP_PARAGRAPH_LI_6: 'ฉันรู้สึกเฉยๆเมื่อใดก็ตามที่ฉันแพ้ในขณะที่เล่นการพนัน?',
  HELP_PARAGRAPH_LI_7: 'ฉันรู้สึกว่ามีแรงกระตุ้นที่จะวางเดิมพันมากขึ้นในคาสิโน เพื่อกลับมาชนะอีกครั้งหลังจากที่เสียไป?',
  HELP_PARAGRAPH_LI_8: 'ฉันเคยปกปิดยอดเสียพนัน โดยการโกหกคนที่ถามหรือไม่ ?',
  HELP_PARAGRAPH_LI_9: 'ฉันเริ่มเก็บเงินเป็นพิเศษสำหรับการพนันหรือไม่?',
  HELP_PARAGRAPH_LI_10: 'ระบบคัดกรอง ซอฟต์แวร์ปกป้องเยาวชน จากเนื้อหาเว็บที่ไม่เหมาะสม:',
  HELP_PARAGRAPH_LI_11: 'ซอฟต์แวร์การกรอง CYBERsitter อนุญาตให้ผู้ปกครองบล็อก:',
  HELP_TERMS_TITLE_1: 'A. ทั่วไป',
  HELP_TERMS_TITLE_2: 'B. บัญชีสมาชิก',
  HELP_TERMS_TITLE_3: 'C. การฝากและถอน',
  HELP_TERMS_TITLE_4: 'D. โปรโมชั่นคาสิโน',
  HELP_TERMS_TITLE_5: 'E. ข้อพิพาท & การจ่ายเงิน',
  HELP_TERMS_TITLE_6: 'F. กฎการเดิมพันผลิตภัณฑ์',
  HELP_TERMS_TITLE_7: 'G. ระบบคาสิโนและเทคโนโลยี',
  HELP_TERMS_TITLE_8: 'H. ความรับผิดชอบ',
